  //         transformCode={code => '/** @jsx mdx */' + code}
  //         scope={{mdx}}
  //       >
  //         <LivePreview />
  //         <LiveEditor />
  //         <LiveError />
  //       </LiveProvider>
  //     </div>
  //   )
